import React from 'react';
import Card from '../atoms/Card';
import Logo from '../atoms/Logo';

const HeroLogo = () => (<Card>
  <Logo width="125" height="125" fill="none" stroke="#ffffff" strokeWidth="20" strokeMiterlimit="20"/>
</Card>);

export default HeroLogo;



